a {
  text-decoration: none;
}

input,button,textarea{
	font-family:inherit;
}
textarea{
	resize: none;
	overflow: hidden;
}
input::-ms-clear{
	display: none;
}

button{
	cursor: pointer;
	padding: 0;
	border: 0;
}

button::-moz-focus-inner {
	padding: 0;
	border: 0;
}

a, a:visited,a:hover{
	text-decoration: none;
	font-style: normal;
}

ul, ul li, ol li {
	list-style: none;
	padding: 0;
	margin: 0;
}

svg	{
	display: block;
}
